import React ,{ useEffect } from 'react'
import {Link, useNavigate}from 'react-router-dom'
import { Button,Row,Col,ListGroup,Image,Card } from 'react-bootstrap'
import { useDispatch,useSelector } from 'react-redux'
import Message from '../components/Message'
import CheckoutSteps from '../components/CheckoutSteps'
import {createOrder} from '../actions/orderActions.js'
export default function PlaceOrderScreen() {
  const cart=useSelector((state)=>state.cart)
  const dispatch=useDispatch()
  const navigate=useNavigate()
  
  //calculate prices
  const addDecimals=(num)=>{
    return (Math.round(num*100)/100).toFixed(2)
  }
  cart.itemsPrice=addDecimals(cart.cartItems.reduce((acc,item)=>acc+ parseFloat(item.price) * item.qty,0))
  cart.shippingPrice=addDecimals(cart.itemsPrice>100 ?0: 100)
  cart.taxPrice=addDecimals(parseFloat((0.15*cart.itemsPrice).toFixed(2)))
  cart.totalPrice=parseFloat(cart.itemsPrice)+
  parseFloat(cart.shippingPrice)+
    parseFloat(cart.taxPrice)
    
    const orderCreate=useSelector(state=>state.orderCreate)
    const {order,success,error}=orderCreate

    useEffect((_id)=>{
        if (success){
            navigate(`/order/${order._id}`)
        }
    },[success,navigate])
    const placeOrderHandler=()=>{
       
    dispatch(createOrder({
        orderItems:cart.cartItems,
        shippingAddress:cart.shippingAddress,
        paymentMethod:cart.paymentMethod,
        itemsPrice:cart.itemsPrice,
        shippingPrice:cart.shippingPrice,
        taxPrice:cart.taxPrice,
        totalPrice:cart.totalPrice
    }))
  }
    return (
    <>
    <CheckoutSteps step1 step2 step3 step4/>
    <Row>
        <Col md={8}>
            <ListGroup variant='flush'>
                <ListGroup.Item>
                    <h2>shipping</h2>
                    <p>
  <strong>Address:</strong>
  {cart.shippingAddress.address}, {cart.shippingAddress.city},{' '}
  {cart.shippingAddress.postalCode}, {cart.shippingAddress.country}
</p>
                </ListGroup.Item>
              
                <ListGroup.Item>
                <h2>Payment Method</h2>
                <strong>Method :</strong>
                {cart.paymentMethod}
                </ListGroup.Item>

                <ListGroup.Item>
                    <h2>Order Items</h2>
                    {cart.cartItems.length === 0 ?(
                        <Message>YOUR cart is empty</Message>
                    ):(
                        <ListGroup variant='flush'>
                            {cart.cartItems.map((item,index)=>(
                                <ListGroup.Item key={index}>
                                    <Row>
                                        <Col md={1}>
                                        <Image
                                        src={item.image}
                                        alt={item.name}
                                        fluid
                                         rounded/>


                                        </Col>
                                        <Col>
                                        <Link to={`/product/${item.product}`}>
                                            {item.name}
                                        </Link>
                                        </Col>
                                        <Col md={4}>
                                        {parseInt(item.qty)}x ${parseInt(item.price)}=${parseInt(item.qty) * parseInt(item.price)}

                                        
                                        </Col>
                                        
                                    </Row>
                                </ListGroup.Item>
                            ))}
                        </ListGroup>
                    )}
                </ListGroup.Item>
            </ListGroup>

        </Col>
        <Col md={4}>
            <Card>
                <ListGroup variant='flush'>
                    <ListGroup.Item>
                        <h2>order summary </h2>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Row>
                            <Col>Items</Col>
                            <Col>${cart.itemsPrice}</Col>
                        </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Row>
                            <Col>shipping</Col>
                            <Col>${cart.shippingPrice}</Col>
                        </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Row>
                            <Col>Tax</Col>
                            <Col>${cart.taxPrice}</Col>
                        </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <Row>
                            <Col>Total</Col>
                            <Col>${cart.totalPrice}</Col>
                        </Row>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        {error && <Message variant='danger'>{error}</Message>}
                    </ListGroup.Item>
                    <ListGroup.Item>
                    <Button
                    type='button'
                    className='btn-block'
                    disabled={cart.cartItems === 0}
                    onClick={placeOrderHandler}>
                        Place Order
                    </Button>
                    </ListGroup.Item>
                </ListGroup>
            </Card>
        </Col>
    </Row>
    </>
  )
}
